import React, {useState, Fragment, useEffect} from 'react';
import {
  StyleSheet,
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  LogBox,
} from 'react-native';
import {Calendar, CalendarUtils} from 'react-native-calendars';
import {Button, Dialog, GridList, Image, Spacings} from 'react-native-ui-lib';
import {
  image_left_arrowBo,
  image_rightArrow12px,
} from '../../assets/images/images';
import {colors} from '../../assets/color/color';

const CalendarScreen = (props: any) => {
  const originalDate = new Date();
  originalDate.setHours(originalDate.getHours() + 7);
  const formattedTime = originalDate.toISOString().split('T')[0];
  const [selected, setSelected] = useState(formattedTime);
  const [dataYear, setDataYear] = useState<any>([]);
  const [currentMonth, setCurrentMonth] = useState('');
  const [currentYear, setCurrentYear] = useState<any>('');
  const [modalMonth, setModalMonth] = useState(false);
  const [modalyear, setModalYear] = useState(false);
  const [isSelect, setIsSelect] = useState('');

  LogBox.ignoreAllLogs(true);


  const dataMonth = [
    {
      monthNum: '01',
      label: 'January',
      value: 'Jan',
    },
    {
      monthNum: '02',
      label: 'February',
      value: 'Feb',
    },
    {
      monthNum: '03',
      label: 'March',
      value: 'Mar',
    },
    {
      monthNum: '04',
      label: 'April',
      value: 'Apr',
    },
    {
      monthNum: '05',
      label: 'May',
      value: 'May',
    },
    {
      monthNum: '06',
      label: 'June',
      value: 'Jun',
    },
    {
      monthNum: '07',
      label: 'July',
      value: 'Jul',
    },
    {
      monthNum: '08',
      label: 'August',
      value: 'Aug',
    },
    {
      monthNum: '09',
      label: 'September',
      value: 'Sep',
    },
    {
      monthNum: '10',
      label: 'October',
      value: 'Oct',
    },
    {
      monthNum: '11',
      label: 'November',
      value: 'Nov',
    },
    {
      monthNum: '12',
      label: 'December',
      value: 'Dec',
    },
  ];

  const startDate = new Date();
  const month = startDate.getMonth() + 1;
  const year = startDate.getFullYear();
  const day = startDate.getDate() - 7;
  const hours = startDate.getHours();

  const date = new Date(year, month, day, hours);

  useEffect(() => {
    let dataYears = [];
    for (let i = new Date().getFullYear(); i >= 1950; i--) {
      dataYears.push(i);
    }
    setDataYear(dataYears);
    dataMonth.forEach((item: any) => {
      if (item.monthNum == date.getMonth()) {
        setCurrentMonth(item.label);
        setCurrentYear(date.getFullYear());
      }
    });
  }, []);

  const onMonthSelect = (month: any) => {
    const dayCurrent = selected;
    dataMonth.forEach((item: any) => {
      if (item.monthNum == month) {
        setCurrentMonth(item.label);
        setCurrentYear(dayCurrent.substring(0, 4));
        const daySelect = `${dayCurrent.substring(
          0,
          4,
        )}-${month}-${dayCurrent.substring(8, 10)}`;
        setSelected(daySelect);
      }
    });
  };

  const onYearSelect = (year: any) => {
    const isMonth = selected.substring(5, 7);
    const dayCurrent = selected;
    dataMonth.forEach((item: any) => {
      if (item.monthNum == isMonth) {
        setCurrentMonth(item.label);
        setCurrentYear(year);
        const daySelect = `${year}-${isMonth}-${dayCurrent.substring(8, 10)}`;
        setSelected(daySelect);
      }
    });
  };

  const handleSaveMonthYear = (day: any) => {
    dataMonth.forEach((item: any) => {
      if (item.monthNum === day.month) {
        setCurrentMonth(item.label);
        setCurrentYear(day.year);
      }
    });
  };

  const getDate = (count: number) => {
    const date = new Date(selected);
    const newDate = date.setDate(date.getDate() + count);
    return CalendarUtils.getCalendarDateString(newDate);
  };

  const getCurrentMonth = () => {
    dataMonth.forEach((item: any) => {
      if (item.monthNum == month) {
        setCurrentMonth(item.label);
        setCurrentYear(year);
        setSelected(formattedTime);
      }
    });
  };

  const getCurrentYear = () => {
    setCurrentYear(year);
    setSelected(formattedTime);
  };

  const renderMonth = (items: any) => {
    if (items.item.label == currentMonth) {
      setIsSelect(items.item.value);
    }

    let valueBackground =
      isSelect === items.item.value ? colors.primary : undefined;
    let valueText = isSelect === items.item.value ? '#FFF' : colors.colorText;
    return (
      <TouchableOpacity
        style={{
          width: '50%',
          marginHorizontal: 10,
          borderRadius: 8,
          alignItems: 'center',
          backgroundColor: valueBackground,
        }}
        onPress={() => onMonthSelect(items.item.monthNum)}
        key={items.item.index}>
        <Text
          style={{
            fontWeight: '400',
            fontSize: 14,
            lineHeight: 22,
            color: valueText,
            paddingVertical: 10,
          }}>
          {items.item.value}
        </Text>
      </TouchableOpacity>
    );
  };

  const renderYear = (items: any) => {
    if (items.item == currentYear) {
      setIsSelect(items.item);
    }

    let valueBackground = isSelect === items.item ? colors.primary : undefined;
    let valueText = isSelect === items.item ? '#FFF' : colors.colorText;
    return (
      <TouchableOpacity
        style={{
          borderRadius: 8,
          backgroundColor: valueBackground,
          alignItems: 'center',
          justifyContent: 'center',
        }}
        onFocus={() => {
          items.item == currentYear;
        }}
        onPress={() => onYearSelect(items.item)}
        key={items.index}>
        <Text
          style={{
            fontWeight: '400',
            fontSize: 14,
            lineHeight: 22,
            color: valueText,
            paddingVertical: 10,
          }}>
          {items.item}
        </Text>
      </TouchableOpacity>
    );
  };

  const confirmChooseMonth = () => {
    // setSelected(formattedTime);
    setModalMonth(false);
  };

  function dialogChooseMonth(selected: any) {
    const year = selected.substring(0, 4);
    return (
      <Dialog
        onDismiss={() => setModalMonth(false)}
        visible={modalMonth}
        containerStyle={{
          backgroundColor: '#fff',
          borderRadius: 8,
          width: '100%',
          height: '80%',
        }}>
        <View
          style={{
            flex: 1,
            marginTop: 30,
            justifyContent: 'space-between',
            paddingHorizontal: 10,
            alignItems: 'flex-start',
            flexDirection: 'row',
          }}>
          <TouchableOpacity onPress={movePreviousYear}>
            <Image width={20} height={20} source={image_left_arrowBo} />
          </TouchableOpacity>
          <Text
            style={{
              fontSize: 16,
              lineHeight: 24,
              color: colors.colorText,
              fontWeight: '600',
            }}>
            {year}
          </Text>

          <TouchableOpacity onPress={moveNextYear}>
            <Image width={20} height={20} source={image_rightArrow12px} />
          </TouchableOpacity>
        </View>
        <SafeAreaView
          style={{
            flex: 4,
            width: '100%',
            height: '100%',
          }}>
          <GridList
            data={dataMonth}
            numColumns={3}
            itemSpacing={Spacings.s1}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            renderItem={renderMonth}
          />
        </SafeAreaView>
        <View
          style={{
            flex: 1,
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'space-between',
            paddingHorizontal: 16,
            paddingVertical: 12,
            borderWidth: 1,
            borderStyle: 'solid',
            borderTopColor: 'rgba(0, 53, 128, 0.15)',
            borderLeftColor: 'transparent',
            borderRightColor: 'transparent',
            borderBottomColor: 'transparent',
          }}>
          <Button
            onPress={getCurrentMonth}
            labelStyle={{
              fontSize: 14,
              fontWeight: '500',
              color: colors.primary,
            }}
            style={{backgroundColor: 'transparent'}}
            label="Tháng này"
          />
          <View
            style={{
              flexDirection: 'row',
              alignItems: 'center',
              justifyContent: 'center',
              columnGap: 8,
            }}>
            <Button
              borderRadius={4}
              labelStyle={{fontSize: 14, fontWeight: '500', color: colors.text}}
              style={{backgroundColor: '#F2F5F8'}}
              label="Đặt lại"
            />
            <Button
              borderRadius={4}
              onPress={confirmChooseMonth}
              labelStyle={{fontSize: 14, fontWeight: '500', color: '#fff'}}
              style={{backgroundColor: colors.primary}}
              label="Xác nhận"
            />
          </View>
        </View>
      </Dialog>
    );
  }

  function dialogChooseYear(selected: any) {
    const year = selected.substring(0, 4);
    return (
      <Dialog
        onDismiss={() => setModalYear(false)}
        visible={modalyear}
        containerStyle={{
          backgroundColor: '#fff',
          borderRadius: 8,
          width: '100%',
          height: '80%',
        }}>
        <View
          style={{
            flex: 1,
            marginTop: 30,
            justifyContent: 'space-between',
            paddingHorizontal: 10,
            alignItems: 'flex-start',
            flexDirection: 'row',
          }}>
          <TouchableOpacity>
            <Image width={20} height={20} source={image_left_arrowBo} />
          </TouchableOpacity>
          <Text
            style={{
              fontSize: 16,
              lineHeight: 24,
              color: colors.colorText,
              fontWeight: '600',
            }}>
            {year}
          </Text>

          <TouchableOpacity>
            <Image width={20} height={20} source={image_rightArrow12px} />
          </TouchableOpacity>
        </View>
        <SafeAreaView
          style={{
            flex: 4,
            width: '100%',
            height: '100%',
          }}>
          <GridList
            containerWidth={370}
            data={dataYear}
            numColumns={3}
            itemSpacing={Spacings.s1}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            renderItem={renderYear}
          />
        </SafeAreaView>
        <View
          style={{
            flex: 1,
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'space-between',
            paddingHorizontal: 16,
            paddingVertical: 12,
            borderWidth: 1,
            borderStyle: 'solid',
            borderTopColor: 'rgba(0, 53, 128, 0.15)',
            borderLeftColor: 'transparent',
            borderRightColor: 'transparent',
            borderBottomColor: 'transparent',
          }}>
          <Button
            labelStyle={{
              fontSize: 14,
              fontWeight: '500',
              color: colors.primary,
            }}
            onPress={getCurrentYear}
            style={{backgroundColor: 'transparent'}}
            label="Năm này"
          />
          <View
            style={{
              flexDirection: 'row',
              alignItems: 'center',
              justifyContent: 'center',
              columnGap: 8,
            }}>
            <Button
              borderRadius={4}
              labelStyle={{fontSize: 14, fontWeight: '500', color: colors.text}}
              style={{backgroundColor: '#F2F5F8'}}
              label="Đặt lại"
            />
            <Button
              borderRadius={4}
              onPress={() => setModalYear(false)}
              labelStyle={{fontSize: 14, fontWeight: '500', color: '#fff'}}
              style={{backgroundColor: colors.primary}}
              label="Xác nhận"
            />
          </View>
        </View>
      </Dialog>
    );
  }

  const renderHeader = () => {
    return (
      <View
        style={{
          flex: 1,
          flexDirection: 'row',
          justifyContent: 'space-between',
          alignItems: 'center',
          paddingVertical: 10,
          borderBottomWidth: 1,
          borderBlockColor: 'grey',
        }}>
        <View>
          <TouchableOpacity onPress={movePrevious}>
            <Image width={20} height={20} source={image_left_arrowBo} />
          </TouchableOpacity>
        </View>

        <View style={{flexDirection: 'row', columnGap: 12}}>
          <TouchableOpacity onPress={() => setModalMonth(true)}>
            {dialogChooseMonth(selected)}
            <Text
              style={{
                fontWeight: '600',
                fontSize: 16,
                lineHeight: 24,
                color: colors.colorText,
              }}>
              {currentMonth}
            </Text>
          </TouchableOpacity>
          <TouchableOpacity onPress={() => setModalYear(true)}>
            {dialogChooseYear(selected)}
            <Text
              style={{
                fontWeight: '600',
                fontSize: 16,
                lineHeight: 24,
                color: colors.colorText,
              }}>
              {currentYear}
            </Text>
          </TouchableOpacity>
        </View>

        <View>
          <TouchableOpacity onPress={moveNext}>
            <Image width={20} height={20} source={image_rightArrow12px} />
          </TouchableOpacity>
        </View>
      </View>
    );
  };

  const renderCalendarWithPeriodMarkingAndDotMarking = () => {
    return (
      <Fragment>
        <Calendar
          headerStyle={{
            borderWidth: 1,
            borderStyle: 'solid',
            borderTopColor: 'rgba(0, 53, 128, 0.15)',
            borderLeftColor: 'transparent',
            borderRightColor: 'transparent',
            borderBottomColor: 'rgba(0, 53, 128, 0.15)',
          }}
          enableSwipeMonths={false}
          renderHeader={() => renderHeader()}
          current={selected}
          initialDate={selected}
          minDate={getDate(-14)}
          showWeekNumbers={true}
          hideArrows={true}
          allowSelectionOutOfRange
          markingType={'custom'}
          markedDates={{
            [selected]: {
              selected: true,
              disableTouchEvent: true,
              selectedColor: colors.primary,
              selectedTextColor: '#fff',
            },
            // [selected]: { customContainerStyle: { borderColor: colors.primary, borderWidth: 1, borderStyle: "solid" }, textColor: "red" },
            [getDate(1)]: {selected: true, disableTouchEvent: true},
            [getDate(2)]: {selected: true, disableTouchEvent: true},
            [getDate(3)]: {selected: true, disableTouchEvent: true},
            [getDate(4)]: {selected: true, disableTouchEvent: true},
            [getDate(5)]: {
              selected: true,
              activeOpacity: 1,
              disableTouchEvent: true,
            },
            [getDate(6)]: {
              selected: true,
              activeOpacity: 1,
              disableTouchEvent: true,
            },
          }}
          theme={{
            textInactiveColor: colors.primary,
            textSectionTitleDisabledColor: 'grey',
            textSectionTitleColor: colors.primary,
            arrowColor: colors.primary,
            todayTextColor: '#fff',
            todayBackgroundColor: colors.primary,
            selectedDayTextColor: '#fff',
            selectedDayBackgroundColor: colors.primary,
          }}
          onDayPress={day => {
            console.log(day,'day')
            setSelected(day.dateString);
            handleSaveMonthYear(day);
          }}
        />
      </Fragment>
    );
  };

  const setCustomHeaderNewMonth = (next = false) => {
    const add = next ? 1 : -1;
    const month = new Date(selected);
    const newDay = new Date(month.setMonth(month.getMonth() + add));
    dataMonth.forEach((item: any) => {
      if (
        item.monthNum == Number.parseInt(newDay.toISOString().substring(5, 7))
      ) {
        setCurrentMonth(item.label);
        setCurrentYear(newDay.toISOString().substring(0, 4));
        const daySelect = `${newDay.toISOString().substring(0, 4)}-${newDay
          .toISOString()
          .substring(5, 7)}-${newDay.toISOString().substring(8, 10)}`;
        setSelected(daySelect);
      }
    });
  };

  const moveNext = () => {
    setCustomHeaderNewMonth(true);
  };
  const movePrevious = () => {
    setCustomHeaderNewMonth(false);
  };

  const setCustomHeaderNewYear = (next = false) => {
    const add = next ? 1 : -1;
    const daySelectd = new Date(selected);
    const updateDayselect = new Date(
      daySelectd.setFullYear(daySelectd.getFullYear() + add),
    );
    const newYear = updateDayselect.toISOString().split('T')[0];
    dataMonth.forEach((item: any) => {
      if (item.monthNum == Number.parseInt(newYear.substring(5, 7))) {
        setCurrentMonth(item.label);
        setCurrentYear(newYear.substring(0, 4));
        const daySelectStr = `${newYear.substring(0, 4)}-${newYear.substring(
          5,
          7,
        )}-${newYear.substring(8, 10)}`;
        setSelected(daySelectStr);
      }
    });
  };

  const moveNextYear = () => {
    setCustomHeaderNewYear(true);
  };
  const movePreviousYear = () => {
    setCustomHeaderNewYear(false);
  };

  const renderExamples = () => {
    return (
      <Fragment>{renderCalendarWithPeriodMarkingAndDotMarking()}</Fragment>
    );
  };

  return (
    <Dialog
      onDismiss={() => props.setShowCalender(false)}
      visible={props.showCalender}
      containerStyle={{
        backgroundColor: '#fff',
        borderRadius: 8,
      }}>
      {renderExamples()}
      <View
        style={{
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'space-between',
          paddingHorizontal: 16,
          paddingVertical: 12,
          borderWidth: 1,
          borderStyle: 'solid',
          borderTopColor: 'rgba(0, 53, 128, 0.15)',
          borderLeftColor: 'transparent',
          borderRightColor: 'transparent',
          borderBottomColor: 'transparent',
          width: '100%',
        }}>
        <Button
          labelStyle={{fontSize: 14, fontWeight: '500', color: colors.primary}}
          style={{backgroundColor: 'transparent'}}
          label="Tuần này"
        />
        <View
          style={{
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            columnGap: 8,
          }}>
          <Button
            borderRadius={4}
            labelStyle={{fontSize: 14, fontWeight: '500', color: colors.text}}
            style={{backgroundColor: '#F2F5F8'}}
            label="Đặt lại"
          />
          <Button
            borderRadius={4}
            onPress={() => props.setShowCalender(false)}
            labelStyle={{fontSize: 14, fontWeight: '500', color: '#fff'}}
            style={{backgroundColor: colors.primary}}
            label="Xác nhận"
          />
        </View>
      </View>
    </Dialog>
  );
};

export default CalendarScreen;

const styles = StyleSheet.create({
  calendar: {
    marginBottom: 10,
  },
  switchContainer: {
    flexDirection: 'row',
    margin: 10,
    alignItems: 'center',
  },
  switchText: {
    margin: 10,
    fontSize: 16,
  },
  text: {
    textAlign: 'center',
    padding: 10,
    backgroundColor: 'lightgrey',
    fontSize: 16,
  },
  disabledText: {
    color: 'grey',
  },
  defaultText: {
    color: 'purple',
  },
  customCalendar: {
    height: 250,
    borderBottomWidth: 1,
    borderBottomColor: 'lightgrey',
  },
  customDay: {
    textAlign: 'center',
  },
  customHeader: {
    backgroundColor: '#FCC',
    flexDirection: 'row',
    justifyContent: 'space-around',
    marginHorizontal: -4,
    padding: 8,
  },
  customTitleContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    padding: 10,
  },
  customTitle: {
    fontSize: 16,
    fontWeight: 'bold',
    color: '#00BBF2',
  },
  customTitleHeader: {
    fontSize: 16,
    fontWeight: '600',
    color: colors.colorTitie,
  },
  // modalContainer: {

  // },
});
